const createProductBlock = (): DynamicProduct[] => PRODUCT_DATA.map((p, index) => ({
    ...p,
    uniqueKey: `${p.id}-0-${index}`,
}));

const screenToWorld = (x: number, y: number, viewportWidth: number, viewportHeight: number, camera: THREE.PerspectiveCamera, bagDepth: number) => {
    if (viewportWidth === 0 || viewportHeight === 0) return { x: 0, y: 0 };
    
    const fov = camera.fov * (Math.PI / 180);
    const distance = camera.position.z - bagDepth;
    const visibleHeight = 2 * Math.tan(fov / 2) * distance;
    const visibleWidth = visibleHeight * camera.aspect;

    const xRatio = visibleWidth / viewportWidth;
    const yRatio = visibleHeight / viewportHeight;
    
    const targetWorldX = (x - viewportWidth / 2) * xRatio;
    const targetWorldY = -(y - viewportHeight / 2) * yRatio;

    return { x: targetWorldX, y: targetWorldY };
};


const useCart = () => {
    const [cartItems, setCartItems] = useState<CartState>({});

    const handleAddItem = useCallback((product: Product) => {
        setCartItems(c => {
            const existingItem = c[product.id];
            return {
                ...c,
                [product.id]: existingItem ? { ...existingItem, quantity: existingItem.quantity + 1 } : { product, quantity: 1 }
            };
        });
    }, []);

    const handleDecrementItem = useCallback((productId: string) => {
        setCartItems(c => {
            const existingItem = c[productId];
            if (!existingItem) return c;
            
            if (existingItem.quantity > 1) {
                return {
                    ...c,
                    [productId]: { ...existingItem, quantity: existingItem.quantity - 1 }
                };
            } else {
                const newItems = { ...c };
                delete newItems[productId];
                return newItems;
            }
        });
    }, []);
    
    const handleRemoveItem = useCallback((productId: string) => {
        setCartItems(c => {
            const newItems = { ...c };
            delete newItems[productId];
            return newItems;
        });
    }, []);

    const calculateTotal = useMemo(() => Object.values(cartItems).reduce((sum, item) => sum + (item.product.price * item.quantity), 0).toFixed(2), [cartItems]);
    
    const cartCount = useMemo(() => Object.values(cartItems).reduce((sum, item) => sum + item.quantity, 0), [cartItems]);
    
    const groupedItemsList = useMemo(() => Object.values(cartItems).sort((a, b) => a.product.name.localeCompare(b.product.name)), [cartItems]);

    return { 
        groupedItemsList, 
        handleAddItem, 
        handleDecrementItem, 
        handleRemoveItem, 
        calculateTotal, 
        cartCount 
    };
};

const useNotification = () => {
    const showNotification = useCallback((message: string) => {
        const notification = document.getElementById('notification');
        if (notification) {
            notification.innerHTML = message;
            notification.classList.remove('opacity-0', 'scale-95');
            notification.classList.add('opacity-100', 'scale-100', 'animate-pulse-once');
            setTimeout(() => {
                notification.classList.remove('opacity-100', 'scale-100', 'animate-pulse-once');
                notification.classList.add('opacity-0', 'scale-95');
            }, 2000);
        }
    }, []);
    return showNotification;
};

const useStaticScroll = (containerRef: React.RefObject<HTMLDivElement>) => {
    const [dynamicList] = useState<DynamicProduct[]>(createProductBlock);
    const [currentIndex, setCurrentIndex] = useState(0);
    const [showBackToTop, setShowBackToTop] = useState(false);
    
    const activeProduct = dynamicList[currentIndex];

    const handleScroll = useCallback(() => {
        if (!containerRef.current) return;

        const container = containerRef.current;
        const scrollY = container.scrollTop;
        const height = container.clientHeight;
        
        const newIndex = Math.floor((scrollY + height / 2) / height);
        
        if (newIndex >= 0 && newIndex < dynamicList.length && newIndex !== currentIndex) {
            setCurrentIndex(newIndex);
        }
        
        setShowBackToTop(scrollY > height * 0.5); 

    }, [currentIndex, dynamicList.length, containerRef]); 
    
    const handleClickDot = useCallback((targetDotIndex: number) => {
        if (!containerRef.current) return;
        
        const targetScroll = targetDotIndex * containerRef.current.clientHeight;
            
        containerRef.current.scrollTo({ top: targetScroll, behavior: 'smooth' });
        setCurrentIndex(targetDotIndex);
        
    }, [containerRef]);

    const handleBackToTop = useCallback(() => {
        if (!containerRef.current) return;
        containerRef.current.scrollTo({ top: 0, behavior: 'smooth' });
        setCurrentIndex(0);
    }, [containerRef]);


    return { dynamicList, currentIndex, activeProduct, handleScroll, handleClickDot, showBackToTop, handleBackToTop };
};

interface CBDModelProps {
  product: Product;
  onModelLoaded: () => void;
  isActive: boolean;
  flyoutTarget: FlyoutTarget | null;
}

const CBDModel: React.FC<CBDModelProps> = React.memo(({ product, onModelLoaded, isActive, flyoutTarget }) => {
  const mountRef = useRef<HTMLDivElement>(null);
  const modelRef = useRef<THREE.Group | THREE.Mesh | null>(null); 
  const flyoutAnimationRef = useRef<{ active: boolean, targetX: number, targetY: number, startTime: number, startScale: number, startBagX: number, startBagY: number } | null>(null);

  useEffect(() => {
    if (!mountRef.current) return;

    const currentMount = mountRef.current;
    
    const scene = new THREE.Scene();
    const camera = new THREE.PerspectiveCamera(75, currentMount.clientWidth / currentMount.clientHeight, 0.1, 1000);
    const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
    
    renderer.setSize(currentMount.clientWidth, currentMount.clientHeight);
    renderer.setPixelRatio(window.devicePixelRatio);
    currentMount.appendChild(renderer.domElement);
    
    const createFallbackModel = () => {
        const radiusTop = 1.75;
        const radiusBottom = 2;
        const height = 3; 
        
        const bodyGeometry = new THREE.CylinderGeometry(radiusTop, radiusBottom, height, 64);
        const bodyMaterial = new THREE.MeshPhysicalMaterial({
            color: new THREE.Color(product.color),
            metalness: 0.9,
            roughness: 0.1,
            clearcoat: 1.0,
            clearcoatRoughness: 0.05,
            flatShading: false,
        });
        const fallbackModel = new THREE.Mesh(bodyGeometry, bodyMaterial);
        
        const zipGeometry = new THREE.BoxGeometry(1.6 * 1.8, 0.1, 0.35);
        const zipMaterial = new THREE.MeshStandardMaterial({ color: 0xeeeeee, metalness: 1.0, roughness: 0.1 });
        const zipper = new THREE.Mesh(zipGeometry, zipMaterial);
        zipper.position.y = height / 2; 
        zipper.rotation.y = Math.PI / 4; 
        fallbackModel.add(zipper);
        
        fallbackModel.rotation.x = 0.2;

        modelRef.current = fallbackModel;
        scene.add(fallbackModel);
        onModelLoaded();
        
        return [bodyGeometry, bodyMaterial, zipGeometry, zipMaterial];
    };
    
    const cleanupRefs = createFallbackModel(); 

    
    scene.add(new THREE.AmbientLight(0xffffff, 0.4));
    
    const spotLight = new THREE.SpotLight(0xffffff, 80, 20, Math.PI * 0.15, 0.5, 0.5); 
    spotLight.position.set(4, 5, 4);
    spotLight.target.position.set(0, 0, 0); 
    scene.add(spotLight);
    
    const fillLight = new THREE.PointLight(0xffffff, 15, 100);
    fillLight.position.set(-5, 0, 3);
    scene.add(fillLight);

    const rimLight = new THREE.PointLight(0xffffff, 30, 100);
    rimLight.position.set(0, 5, -5);
    scene.add(rimLight);

    const INITIAL_Z = 15;
    const TARGET_Z = 8.5;
    camera.position.z = INITIAL_Z; 
    
    let isDragging = false;
    let previousMousePosition = { x: 0, y: 0 };

    const handlePointerDown = (event: PointerEvent) => {
        isDragging = true;
        previousMousePosition = { x: event.clientX, y: event.clientY };
        currentMount.setPointerCapture(event.pointerId);
    };

    const handlePointerMove = (event: PointerEvent) => {
        if (!isDragging || !modelRef.current) return;

        event.preventDefault();
        event.stopPropagation();
        
        const deltaX = event.clientX - previousMousePosition.x;
        const deltaY = event.clientY - previousMousePosition.y;
        modelRef.current.rotation.y += deltaX * 0.005;
        modelRef.current.rotation.x += deltaY * 0.005;
        previousMousePosition = { x: event.clientX, y: event.clientY };
    };

    const handlePointerUp = () => { isDragging = false; };

    currentMount.addEventListener('pointerdown', handlePointerDown as EventListener);
    currentMount.addEventListener('pointermove', handlePointerMove as EventListener);
    currentMount.addEventListener('pointerup', handlePointerUp as EventListener);
    
    
    const clock = new THREE.Clock();
    const animate = () => {
      requestAnimationFrame(animate);
      
      const model = modelRef.current;
      if (!model) {
          renderer.render(scene, camera);
          return;
      }

      const elapsedTime = clock.getElapsedTime();

      const isFlying = flyoutAnimationRef.current?.active;
      
      const targetY = 0.15; 
      
      if (isActive && !isFlying) {
        if (camera.position.z > TARGET_Z) camera.position.z -= (camera.position.z - TARGET_Z) * 0.1; 
        if (model.position.y < targetY) model.position.y += (targetY - model.position.y) * 0.05; 
      } else if (!isActive) {
        if (camera.position.z < INITIAL_Z) camera.position.z += (INITIAL_Z - camera.position.z) * 0.05; 
        if (model.position.y > 0) model.position.y -= (model.position.y - 0) * 0.05;
      }
      
      if (!isDragging && !isFlying) {
          model.rotation.y += 0.001;
          model.position.y += Math.sin(elapsedTime * 0.5) * 0.001;
      }
      
      if (isFlying) {
        const { targetX, targetY, startTime, startScale, startBagX, startBagY } = flyoutAnimationRef.current!;
        const duration = 800;
        const elapsed = Date.now() - startTime;
        let t = Math.min(1, elapsed / duration); 

        const easeOutQuart = 1 - Math.pow(1 - t, 4); 

        model.position.x = THREE.MathUtils.lerp(startBagX, targetX, easeOutQuart);
        model.position.y = THREE.MathUtils.lerp(startBagY, targetY, easeOutQuart);

        const targetScale = 0.1;
        model.scale.setScalar(THREE.MathUtils.lerp(startScale, targetScale, easeOutQuart));

        model.rotation.y += 0.1;
        model.rotation.x += 0.05;

        if (t >= 1) {
            flyoutAnimationRef.current!.active = false;
        }
      }

      renderer.render(scene, camera);
    };

    const handleResize = () => {
        const width = currentMount.clientWidth;
        const height = currentMount.clientHeight;
        renderer.setSize(width, height);
        camera.aspect = width / height;
        camera.updateProjectionMatrix(); 
    };

    window.addEventListener('resize', handleResize);
    
    if (flyoutTarget && modelRef.current) {
        const viewportWidth = currentMount.clientWidth;
        const viewportHeight = currentMount.clientHeight;
        
        const { x: targetWorldX, y: targetWorldY } = screenToWorld(
            flyoutTarget.endX, 
            flyoutTarget.endY, 
            viewportWidth, 
            viewportHeight, 
            camera, 
            modelRef.current.position.z 
        );
        
        flyoutAnimationRef.current = {
            active: true,
            targetX: targetWorldX,
            targetY: targetWorldY,
            startTime: Date.now(),
            startScale: modelRef.current.scale.x,
            startBagX: modelRef.current.position.x,
            startBagY: modelRef.current.position.y,
        };
    } else {
        flyoutAnimationRef.current = null;
    }

    animate();

    return () => {
      window.removeEventListener('resize', handleResize);
      currentMount.removeEventListener('pointerdown', handlePointerDown as EventListener);
      currentMount.removeEventListener('pointermove', handlePointerMove as EventListener);
      currentMount.removeEventListener('pointerup', handlePointerUp as EventListener);
      
      currentMount.removeChild(renderer.domElement);
      renderer.dispose();
      
      cleanupRefs.forEach(ref => (ref as any).dispose());
    };
  }, [product.id, product.color, onModelLoaded, isActive, flyoutTarget]);

  return (
    <div 
      ref={mountRef} 
      className={`absolute inset-0 w-full h-full cursor-grab transition-opacity duration-700`}
      style={{ touchAction: 'none' }} 
    />
  );
});

